"use client";

import { memo, useMemo, useState } from "react";

import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
} from "recharts";

const BudgetCard = memo(function BudgetCard({
  category,
  allocated,
  isOpen,
  onToggle,
  items,
  onSubcategoryClick,
  selectedSubcategory,
}) {
  const formattedAmount = useMemo(
    () => `‚Ç±${allocated.toLocaleString()}`,
    [allocated]
  );
  

  // Group items by description/subcategory for clickable sections
  const groupedItems = useMemo(() => {
    const groups = {};
    items?.forEach((item) => {
      const key = item.description || "Uncategorized";
      if (!groups[key]) {
        groups[key] = {
          name: key,
          items: [],
          total: 0,
        };
      }
      groups[key].items.push(item);
      groups[key].total += Number(item.amount) || 0;
    });
    return Object.values(groups);
  }, [items]);

  // Calculate allocation percentage for progress bar
  const progressPercentage = useMemo(() => {
    if (!items || items.length === 0) return 0;
    return Math.min((items.length / 15) * 100, 100);
  }, [items]);

  return (
    <div
      className={`relative rounded-xl border border-slate-200 bg-white transition-all duration-300
        ${isOpen 
          ? "shadow-lg ring-1 ring-slate-100" 
          : "shadow-sm hover:shadow-md hover:border-slate-300"
        }
      `}
    >
      {/* STATUS INDICATOR */}
      <div className="absolute right-4 top-4">
        <span className="inline-flex items-center rounded-full bg-emerald-50 px-2.5 py-0.5 text-xs font-medium text-emerald-700">
          {items?.length || 0} Items
        </span>
      </div>

      {/* HEADER */}
      <div className="p-6">
        <div className="flex items-start justify-between">
          <div className="flex-1">
            {/* CATEGORY HEADER */}
            <div className="flex items-center gap-3 mb-4">
              <div className="h-10 w-10 rounded-lg bg-gradient-to-br from-emerald-500 to-teal-500 flex items-center justify-center">
                <span className="text-white text-lg font-bold">
                  {category.charAt(0)}
                </span>
              </div>
              <div>
                <h3 className="text-lg font-semibold text-slate-900">
                  {category}
                </h3>
                <p className="text-sm text-slate-500">
                  {items?.length || 0} allocation entries
                </p>
              </div>
            </div>

            {/* AMOUNT AND STATS */}
            <div className="flex items-end justify-between">
              <div>
                <p className="text-2xl font-bold text-slate-900">
                  {formattedAmount}
                </p>
              </div>

              <div className="text-right">
                <div className="text-xs font-medium text-slate-500 mb-2">
                  Utilization
                </div>
                <div className="flex items-center gap-2">
                  <div className="h-2 w-32 rounded-full bg-slate-100 overflow-hidden">
                    <div 
                      className="h-full rounded-full bg-gradient-to-r from-emerald-400 to-teal-400 transition-all duration-700"
                      style={{ width: `${progressPercentage}%` }}
                    />
                  </div>
                  <span className="text-sm font-semibold text-slate-700">
                    {Math.round(progressPercentage)}%
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* ACTION BUTTON */}
        <div className="mt-6 pt-6 border-t border-slate-100">
          <button
            onClick={onToggle}
            className={`w-full flex items-center justify-center gap-2 rounded-lg px-4 py-3 text-sm font-medium transition-all
              ${isOpen
                ? "bg-slate-50 text-slate-700 border border-slate-200"
                : "bg-emerald-50 text-emerald-700 hover:bg-emerald-100"
              }
            `}
          >
            {isOpen ? (
              <>
                <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 15l7-7 7 7" />
                </svg>
                Hide Breakdown
              </>
            ) : (
              <>
                <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                </svg>
                View Breakdown
              </>
            )}
          </button>
        </div>
      </div>

      {/* DETAILS PANEL */}
      {isOpen && (
        <div className="border-t border-slate-100 animate-fadeIn">
          <div className="p-6">
            {/* SECTION HEADER */}
            

            {/* SUBCATEGORIES */}
            <div className="space-y-4">
              {groupedItems.length > 0 ? (
                groupedItems.map((group, groupIndex) => (
                  <div
                    key={groupIndex}
                    className={`group cursor-pointer rounded-lg border transition-all duration-200 hover:shadow-md
                      ${selectedSubcategory === group.name
                        ? "border-emerald-300 bg-emerald-50"
                        : "border-slate-200 hover:border-slate-300"
                      }
                    `}
                    onClick={() => onSubcategoryClick?.(group.name)}
                  >
                    <div className="p-4">
                      <div className="flex items-center justify-between">
                        <div className="flex items-center gap-3">
                          <div className={`h-2 w-2 rounded-full transition-colors
                            ${selectedSubcategory === group.name
                              ? "bg-emerald-500"
                              : "bg-slate-300 group-hover:bg-slate-400"
                            }
                          `} />
                          <div>
                            <h5 className={`font-medium transition-colors
                              ${selectedSubcategory === group.name
                                ? "text-emerald-700"
                                : "text-slate-700 group-hover:text-slate-900"
                              }
                            `}>
                              {group.name}
                            </h5>
                            <p className="text-xs text-slate-500 mt-1">
                              {group.items.length} entries
                            </p>
                          </div>
                        </div>
                        <div className="text-right">
                          <p className={`text-sm font-semibold transition-colors
                            ${selectedSubcategory === group.name
                              ? "text-emerald-700"
                              : "text-slate-900"
                            }
                          `}>
                            ‚Ç±{group.total.toLocaleString()}
                          </p>
                          <p className="text-xs text-slate-500 mt-1">
                            {((group.total / allocated) * 100).toFixed(1)}% of total
                          </p>
                        </div>
                      </div>

                      {/* EXPANDED VIEW FOR SELECTED SUBCATEGORY */}
                      {selectedSubcategory === group.name && (
                        <div className="mt-4 pt-4 border-t border-emerald-100 animate-fadeIn">
                          <div className="space-y-3">
                            <div className="flex items-center justify-between text-xs text-slate-500">
                              <span>Item Details</span>
                              <span>Amount</span>
                            </div>
                            {group.items.map((item, itemIndex) => (
                              <div
                                key={itemIndex}
                                className="flex items-center justify-between p-3 rounded-lg bg-white border border-slate-100"
                              >
                                <div className="flex items-center gap-3">
                                  <div className="h-6 w-6 rounded-md bg-slate-50 flex items-center justify-center">
                                    <span className="text-xs font-medium text-slate-500">
                                      {itemIndex + 1}
                                    </span>
                                  </div>
                                  <div>
                                    <p className="text-sm font-medium text-slate-700">
                                      {item.description || "No description"}
                                    </p>
                                    {item.year && (
                                      <p className="text-xs text-slate-400 mt-1">
                                        FY {item.year}
                                      </p>
                                    )}
                                  </div>
                                </div>
                                <span className="text-sm font-semibold text-slate-900">
                                  ‚Ç±{Number(item.amount).toLocaleString()}
                                </span>
                              </div>
                            ))}
                          </div>
                        </div>
                      )}
                    </div>
                  </div>
                ))
              ) : (
                <div className="text-center py-8 rounded-lg border-2 border-dashed border-slate-200">
                  <div className="mx-auto h-12 w-12 rounded-full bg-slate-100 flex items-center justify-center mb-3">
                    <span className="text-slate-400">üìÑ</span>
                  </div>
                  <p className="text-sm font-medium text-slate-500">
                    No allocation details available
                  </p>
                  <p className="text-xs text-slate-400 mt-1">
                    Add budget items to see breakdown
                  </p>
                </div>
              )}
            </div>

            {/* SUMMARY FOOTER */}
            {groupedItems.length > 0 && (
              <div className="mt-6 pt-6 border-t border-slate-100">
                <div className="flex items-center justify-between">
                  <div className="text-sm text-slate-600">
                    <span className="font-medium">Total Items:</span> {items.length}
                  </div>
                  <button className="text-sm font-medium text-emerald-600 hover:text-emerald-700">
                    Export Breakdown ‚Üí
                  </button>
                </div>
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  );
});

// Add custom animations
const style = document.createElement('style');
style.textContent = `
  @keyframes fadeIn {
    from { opacity: 0; transform: translateY(-8px); }
    to { opacity: 1; transform: translateY(0); }
  }
  .animate-fadeIn {
    animation: fadeIn 0.3s ease-out;
  }
`;
document.head.appendChild(style);

export default BudgetCard;