"use client";

import { useState, useEffect, useMemo } from "react";

import { useSearchParams } from "next/navigation";
import { supabase } from "../../../lib/supabaseClient";
import BudgetUploadPage from "../expenses/components/budgetUpload";
import { categorizeExpense } from "../dashboard/components/expensePieChart";
import BudgetSummaryCards from "./components/BudgetSummaryCards";

/* =========================
   CATEGORY NORMALIZER
========================= */
function normalizeCategory(cat = "") {
  const t = cat.toLowerCase();

  if (t.includes("personal")) return "Personal Services";
  if (t.includes("mooe")) return "MOOE";
  if (t.includes("20") || t.includes("bdf")) return "20% BDF";
  if (t.includes("gadr") || t.includes("gad")) return "15% GAD";
  if (t.includes("bdr")) return "5% BDRRMF";
  if (t.includes("sk")) return "10% SK Fund";
  if (t.includes("senior")) return "1% Senior & PWD";

  return cat.trim();
}

export default function ExpensesPage() {
  /* =========================
     YEAR
  ========================= */
 const searchParams = useSearchParams();
const selectedYear = Number(searchParams.get("year")) || 2025;

  /* =========================
     UI STATE (UNCHANGED)
  ========================= */
  const [showUploadModal, setShowUploadModal] = useState(false);
  const [showManualModal, setShowManualModal] = useState(false);
  const [expandedCategory, setExpandedCategory] = useState(null);
  const [refreshKey, setRefreshKey] = useState(0);
const [selectedCategory, setSelectedCategory] = useState(null);
const [selectedSubcategory, setSelectedSubcategory] = useState(null);

  /* =========================
     DATA STATE
  ========================= */
  const [budgetRows, setBudgetRows] = useState([]);
  const [transactions, setTransactions] = useState([]);

  /* =========================
     FETCH BUDGETS
  ========================= */
  const fetchBudgets = async () => {
    const { data, error } = await supabase
      .from("budget_uploads")
      .select("id, year, category, description, amount")
      .or(`year.eq.${selectedYear},year.eq.${String(selectedYear)}`);

    
    setBudgetRows(data || []);
  };

  /* =========================
     FETCH TRANSACTIONS
  ========================= */
  const fetchTransactions = async () => {
  const start = `${selectedYear}-01-01`;
  const end = `${selectedYear}-12-31`;

  const { data, error } = await supabase
    .from("transactions")
    .select("id, description, withdrawal, date")
    .gte("date", start)
    .lte("date", end);

  if (error) {
    console.error("Transaction fetch error:", error.message);
    return;
  }

  setTransactions(data || []);
};

  /* =========================
     INITIAL LOAD
  ========================= */
 useEffect(() => {
  fetchBudgets();
  fetchTransactions();
}, [selectedYear, refreshKey]);


  /* =========================
     GROUP BUDGETS
  ========================= */
const groupedBudgets = useMemo(() => {
  const map = {};

  budgetRows.forEach((r) => {
    const cat = normalizeCategory(r.category);

    if (!map[cat]) {
      map[cat] = {
        category: cat,
        allocated: 0,
        items: [],
      };
    }

    map[cat].allocated += Number(r.amount) || 0;
    map[cat].items.push(r);
  });

  return Object.values(map).sort(
    (a, b) => b.allocated - a.allocated
  );
}, [budgetRows]);


  /* =========================
     GROUP EXPENSES
  ========================= */
  const expensesByCategory = useMemo(() => {
    const map = {};

    transactions.forEach((t) => {
      if (!t.withdrawal || t.withdrawal <= 0) return;

      const raw = categorizeExpense(t.description || "");
      if (!raw) return;

      const cat = normalizeCategory(raw);

      if (!map[cat]) map[cat] = 0;
      map[cat] += Number(t.withdrawal);
    });

    return map;
  }, [transactions]);

  const expensesBySubcategory = useMemo(() => {
  const map = {};

  transactions.forEach((t) => {
    if (!t.withdrawal || t.withdrawal <= 0) return;

    const desc = t.description?.trim();
    if (!desc) return;

    if (!map[desc]) map[desc] = 0;
    map[desc] += Number(t.withdrawal);
  });

  return map;
}, [transactions]);
  /* =========================
     COMPARISON DATA (FOR CHART)
  ========================= */
  const comparisonData = useMemo(() => {
    return groupedBudgets.map((b) => ({
      category: b.category,
      allocated: b.allocated,
      spent: expensesByCategory[b.category] || 0,
    }));
  }, [groupedBudgets, expensesByCategory]);


/* =========================
     UI
  ========================= */
  return (
    <div className="min-h-screen bg-slate-50 ">
      {/* HEADER */}
      <div className="rounded-2xl bg-gradient-to-r from-emerald-700 to-emerald-800 px-8 py-4 text-white shadow-lg">
        <div className="mb-2 flex items-center justify-between">
          <div>
            <div className="flex items-center gap-3">
              <div className="rounded-xl bg-white/10 p-2 backdrop-blur-sm">
                <span className="text-xl">üìä</span>
              </div>
              <div>
                <h1 className="text-2xl font-bold tracking-tight">
                  Budget Allocation Dashboard
                </h1>
                
              </div>
            </div>
          </div>

          <div className="flex gap-3">
            <button
              onClick={() => setShowUploadModal(true)}
              className="flex items-center gap-2 rounded-xl bg-white px-5 py-3 font-semibold text-emerald-700 shadow-lg transition-all hover:scale-[1.02] hover:shadow-xl"
            >
              <span className="text-lg">üì§</span>
              <span>Upload File</span>
            </button>
            <button
              onClick={() => setShowManualModal(true)}
              className="flex items-center gap-2 rounded-xl bg-emerald-500 px-5 py-3 font-semibold text-white shadow-lg transition-all hover:scale-[1.02] hover:bg-emerald-600 hover:shadow-xl"
            >
              <span className="text-lg">‚ûï</span>
              <span>Add Budget</span>
            </button>
          </div>
        </div>

        
        </div>
      {/* MODALS */}
      {showUploadModal && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/60 backdrop-blur-sm">
          <div className="mx-4 w-full max-w-lg rounded-2xl bg-white shadow-2xl">
            <div className="flex items-center justify-between border-b p-6">
              <div>
                <h2 className="text-xl font-bold text-slate-800">Upload Budget File</h2>
                <p className="text-sm text-slate-500">Upload CSV or Excel files for bulk import</p>
              </div>
              <button
                onClick={() => setShowUploadModal(false)}
                className="rounded-lg p-2 text-slate-400 hover:bg-slate-100 hover:text-slate-600"
              >
                <svg className="h-5 w-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>
            <div className="p-6">
              <BudgetUploadPage
                mode="picker"
                selectedYear={selectedYear}
                onSuccess={() => {
                  fetchBudgets();
                  setRefreshKey((k) => k + 1);
                  setShowUploadModal(false);
                }}
              />
            </div>
          </div>
        </div>
      )}

      {showManualModal && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/60 backdrop-blur-sm">
          <div className="mx-4 w-full max-w-4xl rounded-2xl bg-white shadow-2xl">
            <div className="flex items-center justify-between border-b p-6">
              <div>
                <h2 className="text-xl font-bold text-slate-800">Add Budget Entry</h2>
                <p className="text-sm text-slate-500">Manually add a new budget allocation</p>
              </div>
              <button
                onClick={() => setShowManualModal(false)}
                className="rounded-lg p-2 text-slate-400 hover:bg-slate-100 hover:text-slate-600"
              >
                <svg className="h-5 w-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>
            <div className="p-6">
              <BudgetUploadPage
                mode="form"
                selectedYear={selectedYear}
                onSuccess={() => {
                  fetchBudgets();
                  setRefreshKey((k) => k + 1);
                  setShowManualModal(false);
                }}
              />
            </div>
          </div>
        </div>
      )}

      {/* CONTENT */}
      <div className="mt-8">
        <div className="mb-6 flex items-center justify-between">
          <div>
          </div>
          <div className="text-sm text-slate-500">
            Showing {groupedBudgets.length} categories
          </div>
        </div>

        {/* BUDGET CARDS GRID */}
        {groupedBudgets.length > 0 ? (
          <div className="grid grid-cols-1 gap-6 md:grid-cols-2 xl:grid-cols-3">
            {groupedBudgets.map((g) => {
              const isOpen = expandedCategory === g.category;
              return (
                  <BudgetSummaryCards                 
                  key={g.category}
                  category={g.category}
                  allocated={g.allocated}
                  expensesByCategory={expensesByCategory}
                    expensesBySubcategory={expensesBySubcategory}
                  isOpen={isOpen}
                  onToggle={() => setExpandedCategory(isOpen ? null : g.category)}
                  items={g.items}
                />
              );
            })}
          </div>
        ) : (
          <div className="rounded-2xl border-2 border-dashed border-slate-200 bg-white p-12 text-center">
            <div className="mx-auto mb-4 flex h-16 w-16 items-center justify-center rounded-full bg-slate-100">
              <span className="text-2xl">üìä</span>
            </div>
            <h3 className="mb-2 text-lg font-semibold text-slate-700">No Budget Data</h3>
            <p className="mb-6 text-slate-500">Get started by uploading a file or adding a budget manually</p>
            <div className="flex justify-center gap-3">
              <button
                onClick={() => setShowUploadModal(true)}
                className="rounded-lg bg-emerald-50 px-4 py-2 text-sm font-medium text-emerald-700 hover:bg-emerald-100"
              >
                Upload File
              </button>
              <button
                onClick={() => setShowManualModal(true)}
                className="rounded-lg bg-emerald-600 px-4 py-2 text-sm font-medium text-white hover:bg-emerald-700"
              >
                Add Budget
              </button>
            </div>
          </div>
        )}

        
            
        
    
      </div>
    </div>
  );
}